@import url(https://fonts.googleapis.com/css?family=IBM+Plex+Mono:100,100i,200,200i,300,300i);
@import url(https://fonts.googleapis.com/css?family=IBM+Plex+Sans:100,100i,200,200i,400,500);
body {
  margin: 0;
  padding: 0;
  font-family: 'IBM Plex Serif', serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/*
  Evan MacHale - N00150552
  01.04.19
  App.scss
*/
html,
body {
  height: 100%;
  width: 100%;
  margin: 0;
  padding: 0;
  left: 0;
  top: 0;
  font-size: 100%; }

a {
  text-decoration: none;
  font-family: inherit;
  font-weight: inherit; }

a:hover {
  cursor: pointer; }

* {
  color: #151515;
  line-height: 1.3;
  font-family: "IBM Plex Mono", monospace;
  font-weight: 100; }

.mdc-typography,
.mdc-typography--headline2,
.mdc-typography--headline3,
.mdc-typography--headline5,
.mdc-typography--headline6,
.mdc-typography--subtitle1,
.mdc-typography--subtitle2,
.mdc-typography--caption,
.mdc-typography--button,
.mdc-typography--overline {
  font-family: "IBM Plex Sans", sans-serif;
  line-height: 1.3; }

.mdc-typography--body1,
.mdc-typography--body2 {
  font-family: "IBM Plex Mono", monospace; }

.type-light {
  font-weight: 200; }

.type-regular {
  font-weight: 400; }

.type-heavy {
  font-weight: 700; }

.left {
  text-align: left; }

.right {
  text-align: right; }

.center {
  text-align: center;
  margin-left: auto;
  margin-right: auto; }

.justify {
  text-align: justify; }

.hidden-sm {
  display: none; }

/*
  Banner + Title
*/
.heading {
  height: 100vh;
  padding: 0rem; }

.title {
  padding-top: 15%;
  font-weight: 400; }

.title-link {
  border-bottom: solid 5px;
  -webkit-transition: color 0.5s linear;
  transition: color 0.5s linear; }

.title-link:hover, .visit:hover, .footer-link:hover {
  color: blue; }

/*
  Showcase sections
*/
aside {
  font-family: "IBM Plex Mono", monospace;
  font-weight: 300;
  color: blue; }

.showcase-titles {
  font-family: "IBM Plex Sans", sans-serif;
  font-weight: 400; }

.showcase-titles span {
  font-family: "IBM Plex Sans", sans-serif;
  font-weight: 300;
  color: #666666; }

.section-grid {
  padding-bottom: 5rem;
  padding-top: 10rem; }

.visit {
  font-family: "IBM Plex Sans", sans-serif;
  font-size: 2rem;
  font-weight: normal;
  border-bottom: solid 2px;
  -webkit-transition: color 0.5s linear;
  transition: color 0.5s linear; }

.sketch-grid {
  padding: 0rem; }

.sketch-grid-bottom {
  padding-bottom: 20rem; }

.img {
  height: 100%;
  width: 100%; }

.iframe {
  height: 85vh;
  width: 100%; }

.footer {
  height: 100vh;
  padding-top: 4rem;
  padding-top: 5rem; }

@media screen and (max-width: 768px) {
  .title {
    line-height: 45px;
    font-size: 35px;
    margin-bottom: 10vh; }
  .title-link {
    border-bottom: solid 2px;
    color: blue; }
  .subtitle {
    margin-bottom: 25vh; }
  .heading {
    padding-top: 2vh; }
  .footer p {
    font-size: 30px; } }

